}

.bg-lngr-white {
    background-image: var(--bg-lngr-white);
}

.bg-lngr-black-before::before {
    background-image: var(--bg-lngr-black);
    content: '';
    display: block;
    height: 100%;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
}

.bg-lngr-primary {
    background-image: var(--bg-lngr-primary);
}

@include lg-max {
    .bg-lngr-primary {
        --bg-lngr-primary: linear-gradient(to bottom, transparent 0%, transparent -1%, #180f08 60%, #180f08 100%);
    }
}

.bg-lngr-primary-2 {
    background-image: var(--bg-lngr-primary-2);
}

// filter, box, text shadow

.txt-shad-primary {
    text-shadow: var(--txt-shad-primary);
}

.bx-shad-primary {
    box-shadow: var(--bx-shad-primary);
}
  
.fltr-ds-primary {
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

