@include lg-min {
    // background color

    .bg-clr-lg-inh {
        background-color: inherit;
    }

    .bg-clr-lg-tra, .bg-clr-lg-tra-h:hover {
        background-color: transparent;
    }

    .bg-clr-lg-crc {
        background-color: currentColor;
    }

    .bg-clr-lg-white-opc-0-03 {
        background-color: rgba(255, 255, 255, .03);
    }

    .bg-clr-lg-primary, .bg-clr-lg-primary-h:hover {
