.wheel-box {
    width: 100%;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
    border-radius: 5px;
    position: relative;
    z-index: 1;
    height: 430px;
    // overflow: hidden;
  }

  .wheel-box__col {
    min-width: 0;
    position: relative;
    z-index: 1;
    flex-grow: 1;
    flex-shrink: 1;
  }

  .wheel-result-wrapper-bg {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    z-index: 1;
    border-radius: 50%;
    overflow: hidden;
  }

  .wheel-box__col::before,
  .wheel-result-wrapper-bg::before {
    content: '';
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    pointer-events: none;
    background: linear-gradient(to bottom, transparent 22%, #0a0500 50%, #0a0500)
  }

  .wheel-result-wrapper {
    background: url(../img/wheel/inner-bg.webp) no-repeat top center, linear-gradient(to bottom, #5e5e5e 30%, transparent 50%, transparent);
    background-size: contain;
    z-index: 100;
    background-color: #14100b;
    border-top: 2px solid transparent;
    background-clip: padding-box;
  }
  
  .wheel-result {
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 200;
  }
  
  .wheel-result__button {
    opacity: 0;
    -webkit-transition: opacity .3s ease-in-out;
    -o-transition: opacity .3s ease-in-out;
    transition: opacity .3s ease-in-out;
    pointer-events: none;
  }
  
  .wheel-result__button--show {
    opacity: 1;
    pointer-events: auto;
  }
  
