  //   -ms-transform: translateX(-50%);
  //   transform: translateX(-50%);
  // }
  
  .wheel-result__reward {
    opacity: 0;
    -webkit-transition: opacity .3s ease-in-out;
    -o-transition: opacity .3s ease-in-out;
    transition: opacity .3s ease-in-out;
    pointer-events: none;
  }
  
  .wheel-result__reward--show {
    opacity: 1;
    pointer-events: auto;
  }
  
  .wheel-result__reward-desc {
    width: 100%;
    font-size: 14px;
    line-height: 14px;
    text-align: center;
    color: #ff9e00;
  }
  
  .wheel-result__reward-name {
    width: 100%;
    font-size: 20px;
    line-height: 20px;
    font-weight: 700;
    text-align: center;
    color: #fff;
    margin-top: 3px;
  }
  
  @media (max-width: 576px) {
    .wheel-result__reward-name {
      font-size: 18px;
      line-height: 18px;
    }
  }
  
  .wheel-result__processing {
    width: 100%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    text-align: center;
    opacity: 0;
    -webkit-transition: opacity .3s ease-in-out;
    -o-transition: opacity .3s ease-in-out;
    transition: opacity .3s ease-in-out;
    pointer-events: none;
  }
  
  .wheel-result__processing--show {
    opacity: 1;
    pointer-events: auto;
  }
  
  .wheel-result__processing-text {
    width: 100%;
    font-size: 14px;
    line-height: 14px;
    text-align: center;
    color: #b5b4b1;
  }
  
  .wheel-result__brogress {
    width: 200px;
    height: 4px;
    margin: 8px auto 0;
    border-radius: 3px;
    background: #20201e;
  }
  
  .wheel-result__brogress-bar {
    width: 0%;
    height: 100%;
    background: #ff9e00;
    -webkit-box-shadow: 0px 0px 10px rgba(255, 158, 0, 0.5);
    box-shadow: 0px 0px 10px rgba(255, 158, 0, 0.5);
    border-radius: 3px;
  }
  
  .wheel {
    position: relative;
    border-radius: 100%;
    padding: 10px;
    margin: 0 auto;
    padding: 2px;
    background: linear-gradient(to bottom, #5e5e5e -130%, transparent);
  }
  
