  .wheel__update {
    position: absolute !important;
    z-index: 3;
    top: 0;
    left: 0;
    transition: opacity .5s ease-in-out;
    opacity: 0;
    pointer-events: none;
  }
  
  .wheel__update:hover {
    -webkit-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
  }
  
  .wheel__update--show {
    opacity: 1;
    pointer-events: auto;
  }
  
  .wheel__container {
    width: 100%;
    height: 100%;
    border-radius: 100%;
    overflow: hidden;
    position: relative;
    z-index: 1;
    background: #31302d;
  }
  
  .wheel__sector {
    width: 50%;
    height: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform-origin: 0px 0px;
    -ms-transform-origin: 0px 0px;
    transform-origin: 0px 0px;
  }
  
  .wheel__item-icon {
    // width: 48px;
    // height: 48px;
    position: absolute;
    z-index: 2;
    // top: -22px;
    // left: 100px;
    max-width: 40px;
    bottom: calc(100% - 50px);
    left: calc(100% - 54px);
    transform-origin: center;
    transform: rotate(calc(360deg / 12 / 2 - .5deg));
  }
  
  .wheel__item-icon img {
    display: block;
    width: 100%;
    // height: 100%;
    // -webkit-transform: rotate(90deg);
    // -ms-transform: rotate(90deg);
    // transform: rotate(90deg);
  }
  
