@mixin xxxl-min {
  @media (min-width: (1600px)) {
    @content;
  }
}

@mixin xxl-min {
  @media (min-width: (1400px)) {
    @content;
  }
}

@mixin xl-min {
  @media (min-width: (1200px)) {
    @content;
  }
}

@mixin lg-min {
  @media (min-width: (992px)) {
    @content;
  }
}

@mixin md-min {
  @media (min-width: (768px)) {
    @content;
  }
}

@mixin sm-min {
  @media (min-width: (576px)) {
    @content;
  }
}

@mixin xsm-min {
  @media (min-width: (346px)) {
    @content;
  }
}


