.content * {
  margin: revert;
  padding: revert;
}

.content > * {
  margin-top: 20px;
}

.content > *:first-child {
  margin-top: 0;
}

.content {
  color: #b9b9b9;
}
.content p {
  line-height: 2.0332;
}
.content > img {
  border-radius: 14px;
  vertical-align: middle;
}
.content h1, .content h2, .content h3, .content h4, .content h5, .content h6 {
  font-family: "CinzelDecorative", sans-serif;
  font-weight: 700;
  color: var(--color-primary-2);
  margin-bottom: 20px;
  margin-top: 30px;
}
.content h1 {
  font-size: 42px;
}
.content h2 {
  font-size: 32px;
}
.content h3 {
  font-size: 26px;
}
.content h4 {
  font-size: 22px;
}
.content h5 {
  font-size: 19px;
}
.content h6 {
  font-size: 16px;
}
.content ul + ul {
  margin-top: 28px;
}
.content a {
  color: var(--color-primary-2);
}
.content a:hover {
  color: var(--color-white);
}

.table-1 {
  border-spacing: 20px 20px;
  width: calc(100% + 40px);
  margin-left: -20px;
}
.table-1 td {
  border: 1px solid #3a352e;
  border-radius: 12px;
  height: 40px;
  padding: 10px;
  text-align: center;
  background-color: #211e1c;
  color: var(--color-white);
  text-transform: uppercase;
  font-size: 13px;
}
.table-1 td:nth-child(odd) {
  background-color: #322d25;
}

.list-1,
.list-2,
.list-3 {
  padding-left: 0;
  list-style: none;
}
.list-1 li,
.list-2 li,
.list-3 li {
  position: relative;
  z-index: 1;
}
.list-1 li::before,
.list-2 li::before,
.list-3 li::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

.list-1 li::before,
.list-2 li::before {
  background-color: var(--color-primary-2);
  transform: rotate(45deg);
}

.list-1 li {
  padding-left: 32px;
  font-size: 20px;
  color: var(--color-white);
  line-height: 1.8;
}
.list-1 li::before {
  height: 10px;
  width: 10px;
  margin-top: 13px;
}

.list-2 li {
  padding-left: 26px;
}
.list-2 li::before {
  height: 8px;
  width: 8px;
  margin-top: 10px;
}
.list-2 li + li {
  margin-top: 2px;
}

.list-3 li {
  display: flex;
  gap: 14px;
  align-items: start;
}
.list-3 li span:first-child {
  flex-shrink: 0;
}
.list-3 li span:first-child img {
  max-width: 26px;
}
.list-3 li span:last-child {
  padding-top: 3px;
}
.list-3 li + li {
  margin-top: 8px;
}

.spoiler {
  position: relative;
  z-index: 1;
}

.spoiler [data-js-toggle-button] {
  border: none;
  border-radius: 14px;
  background-color: #6c4124;
  font-family: "CinzelDecorative", sans-serif;
  font-size: 18px;
  color: var(--color-white);
  padding: 14px 57px 14px 17px;
  display: block;
  width: 100%;
  cursor: pointer;
  font-weight: bold;
  text-align: left;
  line-height: 1.2;
}
.spoiler [data-js-toggle-button]::before {
  content: "";
  left: 1px;
  right: 1px;
  top: 0;
  bottom: 0;
  position: absolute;
  z-index: -1;
  background-color: #2d2922;
  opacity: 0;
  transition: all 0.3s ease;
  transition-delay: 0.3s;
  border-top-left-radius: inherit;
  border-top-right-radius: inherit;
}
.spoiler [data-js-toggle-button].active::before {
  opacity: 1;
  transition-delay: 0s;
}

.arrow-button {
  position: relative;
}

.arrow-button::after {
  transition: all 0.3s ease;
  content: "";
  position: absolute;
  height: 7px;
  width: 13px;
  background: url(../img/arrow-bottom-3.webp) no-repeat center;
  right: 26px;
  top: 50%;
  margin-top: -3.5px;
}

.arrow-button.active::after,
.actvive > .arrow-button::after {
  transform: rotate(180deg);
}

.spoiler [data-js-toggle-button-parent] + [data-js-toggle-button-parent] {
  margin-top: 26px;
}

.spoiler [data-js-toggle-block-wrapper] {
  overflow: hidden;
  color: #999992;
  position: relative;
  z-index: 1;
  padding: 0;
}
.spoiler [data-js-toggle-block-wrapper]::before {
  content: "";
  left: 1px;
  right: 1px;
  top: 0;
  bottom: 0;
  position: absolute;
  z-index: -1;
  background-color: #2d2922;
  border-radius: 0 0 14px 14px;
}
.spoiler [data-js-toggle-block-wrapper] p {
  padding: 0;
  margin: 0;
  line-height: 1.625;
}

.spoiler [data-js-toggle-block] {
  padding: 0;
  margin: 0;
}
.spoiler [data-js-toggle-block] > div {
  padding: 14px;
}

@media (max-width: 991px) {
  .content > * {
    margin-top: 10px;
  }
  .content .list-1 li {
    font-size: 16px;
  }
  .content .list-1 li::before {
    margin-top: 9px;
  }
  .content h1 {
    font-size: 30px;
  }
  .content h2 {
    font-size: 24px;
  }
  .content h3 {
    font-size: 20px;
  }
  .content h4 {
    font-size: 18px;
  }
  .content h5 {
    font-size: 16px;
  }
  .content h6 {
    font-size: 14px;
  }
  .menu-link {
    padding-left: 0;
  }
  .menu-link:hover::after, .menu-link.active::after {
    left: 25px;
  }
  .points-link.active::before, .points-item:hover .points-link::before, .menu-link:hover::before, .menu-link.active::before {
    transform: scale(1.1);
  }
  .menu-item + .menu-item {
    padding-left: 20px;
  }
  .spoiler [data-js-toggle-button] {
    font-size: 14px;
    padding-top: 12px;
    padding-bottom: 12px;
  }
  .list-3 li span:last-child {
    padding-top: 6px;
  }
}