

.btn {
  min-height: 61px;
  // padding: 0 54px;
  display: flex;
  border: none;
  background-color: transparent;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  -moz-column-gap: 10px;
  column-gap: 10px;
  position: relative;
  white-space: nowrap;
  z-index: 1;
  background: url(../img/button/border/large/btn-border-left-bg.webp) left center no-repeat, url(../img/button/border/large/btn-border-right-bg.webp) right center no-repeat, url(../img/button/border/large/btn-border-repeat-bg.webp) left 46px center no-repeat, url(../img/button/border/large/btn-border-repeat-bg.webp) right 46px center no-repeat, url(../img/button/border/large/btn-border-middle-bg.webp) right 50% center no-repeat;
  background-size: auto 100%, auto 100%, calc(50% - 52px) 100%, calc(50% - 52px) 100%, auto 100%;
  cursor: pointer;
  perspective: 1000px;
  perspective-origin: center center;
  color: var(--color-white);

  &::before {
    content: '';
    height: 6px;
    width: 6px;
    background-color: var(--color-primary-2);
    transform: rotate(-138deg) translateX(50%);
    left: 50%;
    top: -3px;
    position: absolute;
    margin-left: -.7px;
  }

  &::after {
    content: '';
    display: block;
    position: absolute;
    left: 18px;
    top: 7px;
    bottom: 7px;
    right: 18px;
    z-index: -1;
    background: center no-repeat;
    transition: filter .3s ease;
    clip-path: polygon(0% 0%, calc(100% - 28px) 0%, 100% 50%, calc(100% - 28px) 100%, 100% 100%, 100% 100%, 28px 100%, 0% 50%, 28px 0%);
  }

  &--primary::after {
    background-color: var(--btn-primary-bg-color);
    box-shadow: inset 5px 3px 18px 6px #a84822;
  }

  &--secondary::after {
    background-color: #8c3900;
    background-image: radial-gradient(#c45500 50%, rgba(0, 0, 0, .01) 63%, #8c3900 100%);
    background-size: 100% 174%;
  }

  &--quaternary::after {
    background-color: #470099;
    background-image: radial-gradient(#6900d6 50%, rgba(0, 0, 0, .01) 63%, #40008c 100%);
    background-size: 100% 174%;
  }

  &--quinary::after {
    background-color: #232323;
  }

  &--primary-bg::after {
    background-image: url(../img/button/button-background.webp);
  }

&:hover::after,
  &--tertiary:hover,
  &--start:hover {
    filter: brightness(130%);
  }

  &-content {
    min-width: 144px;

    &--192 {
      min-width: 192px;
    }
  }

  &.mnh-54 {
    background-position:
                        left center,
                        right center,
                        left 40px center,
                        right 40px center,
                        right 50% center;
    background-size: auto 100%, auto 100%, calc(50% - 44px) 100%, calc(50% - 44px) 100%, auto 100%;
  }

  &.mnh-42 {
    background: url(../img/button/border/small/btn-border-left-bg.webp) left center no-repeat, url(../img/button/border/small/btn-border-right-bg.webp) right center no-repeat, url(../img/button/border/small/btn-border-repeat-bg.webp) left 32px center no-repeat, url(../img/button/border/small/btn-border-repeat-bg.webp) right 32px center no-repeat, url(../img/button/border/small/btn-border-middle-bg.webp) right 50% center no-repeat;
    background-size: auto 100%, auto 100%, calc(50% - 37px) 100%, calc(50% - 37px) 100%, auto 100%;

    &::before {
      height: 5px;
      width: 5px;
      top: -3px;
      margin-left: -.5px;
    }
  }
  
  &.mnh-42 &-content {
    min-width: 88px;
  }

  &.mnh-69 {
    // padding-left: 69px;
    // padding-right: 69px;
    background-position:
                        left center, right center, left 52px center, right 52px center, right 50% center;
    background-size: auto 100%, auto 100%, calc(50% - 58px) 100%, calc(50% - 58px) 100%, auto 100%;
  }

  &.mnh-69::after {
    top: 8px;
    bottom: 8px;
    left: 20px;
    right: 20px;
    clip-path: polygon(0% 0%, calc(100% - 33px) 0%, 100% 50%, calc(100% - 33px) 100%, 100% 100%, 100% 100%, 33px 100%, 0% 50%, 33px 0%);
  }

  &--2 {
    background: url(../img/button/border-button-big-left-2.webp) left center no-repeat, url(../img/button/border-button-big-right-2.webp) right center no-repeat, url(../img/button/border-button-big-middle-2.webp) left 46px center no-repeat, url(../img/button/border-button-big-middle-2.webp) right 46px center no-repeat, url(../img/button/border-button-big-center-2.webp) right 50% center no-repeat;
  }

  &--2.mnh-69 {
    // padding-left: 61px;
    // padding-right: 61px;
    background-position: left center, right center, left 41px center, right 42px center, right 50% center;
    background-size: auto 100%, auto 100%, calc(50% - 50px) 100%, calc(50% - 50px) 100%, auto 100%;
  }

  &--2.mnh-69::after {
    top: 8px;
    bottom: 8px;
    left: 10px;
    right: 10px;
    clip-path: polygon(0% 0%, calc(100% - 33px) 0%, 100% 50%, calc(100% - 33px) 100%, 100% 100%, 100% 100%, 33px 100%, 0% 50%, 33px 0%);
  }

  &--start {
    background: url("../img/button/button-bg.webp") no-repeat top center;
    clip-path: none;
    height: 109px;
    width: 459px;
    color: #5c4026;
    background-size: cover;

    &::before, &::after {
      display: none;
    }

    &--primary {
      background-image: url("../img/button/button-bg-2.webp");
    }

    &-78 {
      height: 78px;
      min-width: 325px;
      width: auto;
    }
  }
}

.btn:disabled, .btn[disabled] {
  opacity: 0.3;
  cursor: not-allowed;
}

@include lg-max {
  .btn {
    min-height: 51px;
    
    background-position: left center, right center, left 39px center, right 39px center, right 50% center;
    background-size: auto 100%, auto 100%, calc(50% - 44px) 100%, calc(50% - 44px) 100%, auto 100%;

    &-content {
      min-width: 84px;
    }

    &::after {
      clip-path: polygon(0% 0%, calc(100% - 21px) 0%, 100% 50%, calc(100% - 21px) 100%, 100% 100%, 100% 100%, 22px 100%, 0% 50%, 22px 0%);
    }

    &.mnh-69 {
      min-height: 59px;
      background-position: left center, right center, left 45px center, right 45px center, right 50% center;
      background-size: auto 100%, auto 100%, calc(50% - 50px) 100%, calc(50% - 50px) 100%, auto 100%;

      &::after {
        left: 16px;
        right: 16px;
        top: 6px;
        bottom: 6px;
        clip-path: polygon(0% 0%, calc(100% - 28px) 0%, 100% 50%, calc(100% - 28px) 100%, 100% 100%, 100% 100%, 28px 100%, 0% 50%, 28px 0%);
      }
    }

    &.mnh-mob-49 {
      min-height: 49px;
      background-position: left center, right center, left 39px center, right 39px center, right 50% center;
      background-size: auto 100%, auto 100%, calc(50% - 46px) 100%, calc(50% - 46px) 100%, auto 100%;

      &::after {
        clip-path: polygon(0% 0%, calc(100% - 22px) 0%, 100% 50%, calc(100% - 22px) 100%, 100% 100%, 100% 100%, 22px 100%, 0% 50%, 22px 0%);
      }
    }
    

    &--2.mnh-69 {
      // padding-left: 51px;
      // padding-right: 51px;
      min-height: 59px;
      background-position: left center, right center, left 35px  center, right 35px  center, right 50% center;
      background-size: auto 100%, auto 100%, calc(50% - 42px) 100%, calc(50% - 42px) 100%, auto 100%;

      &::after {
        top: 6px;
        bottom: 6px;
        left: 9px;
        right: 9px;
        clip-path: polygon(0% 0%, calc(100% - 27px) 0%, 100% 50%, calc(100% - 27px) 100%, 100% 100%, 100% 100%, 27px 100%, 0% 50%, 27px 0%);
      }
    }
  }
  .btn--start {
    max-width: 252px;
    height: 60px;
    min-width: 252px;
  }
}