.selectbox{
	&_wrapper{
		position: relative;
		cursor: pointer;
		white-space: nowrap;
		display: flex;
		max-width: 100%;
	}
	&_input{
		border: 1px solid var(--color-dark-11);
		background-color: var(--color-dark-10);
		color: var(--color-gray-8);
		min-height: 50px;
		padding-left: 28px;
		padding-right: 58px;
		display: flex;
		align-items: center;
		border-radius: 16px;
		flex-grow: 1;
		flex-shrink: 1;
		width: 100%;

		@include lg-min {
			min-height: 60px;
		}

		@include xxl-min {
			min-width: 318px;
		}

		&_value{
			overflow: hidden;
			min-width: 0;
			text-overflow: ellipsis;
			max-width: 100%;
		}
		&_arrow {
			transition: all .2s ease;
			font-size: 8px;
			position: absolute;
			right: 24px;
			top: 50%;
			margin-top: -3.5px;
			height: 7px;
			width: 14px;
			background: url(../img/arrow-bottom.webp) no-repeat center;
			display: block;
		}
	}
	&_list{
		display: none;
		position: absolute;
		left: 0;
		top: 100%;
		list-style-type: none;
		margin: 10px 0 0;
		padding: 0;
		border-radius: 12px;
		width: calc(100% - 2px);
		overflow-y: auto;
		max-height: 160px;
		min-width: 80px;
		z-index: 2;
		border: 1px solid var(--color-dark-11);
		background-color: var(--color-dark-10);
		color: var(--color-gray-8);
		&_item{
			transition: all .2s ease;
			padding: 10px 28px;
			border-bottom: 1px solid var(--color-gray-400);
			&:last-child{
				border-bottom-width:0;
			}
			&:hover{
				background-color: var(--color-gray-500);
				color: var(--color-white);
			}
			&.selected{
				background-color: var(--color-accent);
				color: white;
			}
			&.disabled{
				opacity: 0.5;
				&:hover{
					background-color: white;
				}
			}
		}
	}
	&_open{
		.selectbox{
			&_list{
				display: block;
			}
			&_input{
				&_arrow{
					transform: rotate(-180deg);
				}
			}
		}
	}
	&_direction_bottom{
		.selectbox{
			&_list{
				top: inherit;
				bottom: 100%;
			}
		}
	}
}