@include sm-min {
  
  .mxw-sm-100-p {
    max-width: 100%;
  }
    
  .over-sm-hid {
    overflow: hidden;
  }

  .over-sm-vis {
    overflow: visible;
  }

  .over-sm-au {
    overflow: auto;
  }

  .over-sm-scro {
    overflow: scroll;
  }

  .over-sm-ini {
    overflow: initial;
  }

  .over-sm-inh {
    overflow: inherit;
  }

  .over-y-sm-hid {
    overflow-y: hidden;
  }

  .br-sm-0 {
    border: 0;
  }
  
  .brw-sm-0 {
    border-width: 0;
  }
  
  .brw-sm-1 {
    border-width: 1px;
  }
  
  .brw-sm-1-3 {
    border-width: 1.3px;
  }
  
  .brw-sm-2, .brw-sm-2-f:focus {
    border-width: 2px;
  }
  
  .brw-sm-2-5 {
    border-width: 2.5px;
  }
  
  .brw-sm-3 {
    border-width: 3px;
  }
  
  .brw-sm-4 {
    border-width: 4px;
  }
  
  .brw-sm-5 {
    border-width: 5px;
  }
  
  .brlw-sm-0 {
    border-left-width: 0;
  }
  
  .brlw-sm-1 {
    border-left-width: 1px;
  }
  
  .brlw-sm-2 {
    border-left-width: 2px;
  }
  
  .brlw-sm-3 {
    border-left-width: 3px;
  }
  
  .brrw-sm-0 {
    border-right-width: 0;
  }
  
  .brrw-sm-1 {
    border-right-width: 1px;
  }
  
  .brrw-sm-2 {
    border-right-width: 2px;
  }
  
  .brbw-sm-0 {
    border-bottom-width: 0;
  }
  
  .brbw-sm-1 {
    border-bottom-width: 1px;
  }
  
  .brbw-sm-1-3 {
    border-bottom-width: 1.3px;
  }
  
  .brbw-sm-2 {
    border-bottom-width: 2px;
  }
  
  .brbw-sm-3 {
    border-bottom-width: 3px;
  }
  
  .brtw-sm-0 {
    border-top-width: 0;
  }
  
  .brtw-sm-1 {
    border-top-width: 1px;
  }
  
  .brs-sm-sol {
    border-style: solid;
  }
  
  .brls-sm-sol {
    border-left-style: solid;
  }
  
  .brts-sm-sol {
    border-top-style: solid;
  }
  
  .brbs-sm-sol {
    border-bottom-style: solid;
  }
  
  .brrs-sm-sol {
    border-right-style: solid;
  }
  
  .brs-sm-dash {
    border-style: dashed;
  }
  
  .brs-sm-dot {
    border-style: dotted;
  }
  
  .brr-sm-inh {
    border-radius: inherit;
  }
  
  .brr-sm-50-p {
    border-radius: 50%;
  }
  
  .brtlr-sm-inh {
    border-top-left-radius: inherit;
  }
  
  .brtrr-sm-inh {
    border-top-right-radius: inherit;
  }
  
  .brbrr-sm-inh {
    border-bottom-right-radius: inherit;
  }
  
  .brblr-sm-inh {
    border-bottom-left-radius: inherit;
  }
  
  .brlc-sm-tra {
    border-left-color: transparent;
  }
  
  .tra-sm-br {
    border-color: transparent;
  }
  
  .pe-sm-au {
    pointer-events: auto;
  }

  .pdb-sm-0 {
    padding-bottom: 0;
  }

  .p-sm-abs {
    position: absolute;
  }
  
  .p-sm-fix {
    position: fixed;
  }
  
  .p-sm-rel {
    position: relative;
  }
  
  .p-sm-sta {
    position: static;
  }
  
  .p-sm-sti {
    position: sticky;
  }
  
  .p-sm-ini {
    position: initial;
  }

  .w-sm-fl {
    width: 100%;
  }
  

  .l-sm-50-p {
    left: 50%;
  }

  .l-sm-au {
      left: auto;
  }

  .r-sm-au {
    right: auto;
  }

  .t-sm-au {
    top: auto;
  }

  .t-sm-50-p {
    top: 50%;
  }

  .b-sm-au {
    bottom: auto;
  }

  .t-trX-sm-50-p {
      transform: translateX(-50%);
  }

  .t-trY-sm-50-p- {
    transform: translateY(-50%);
  }

  .trsf-sm-none {
    transform: none;
  }

  @for $i from 0 to 51 {
    .mrt-sm-#{$i * 2} {
      margin-top: 2px * $i;
    }
    .mrt-sm-#{$i * 2}- {
      margin-top: -2px * $i;
    }
    
    .mrr-sm-#{$i * 2} {
      margin-right: 2px * $i;
    }
    .mrr-sm-#{$i * 2}- {
      margin-right: -2px * $i;
    }
  
    .mrb-sm-#{$i * 2} {
      margin-bottom: 2px * $i;
    }
    .mrb-sm-#{$i * 2}- {
      margin-bottom: -2px * $i;
    }
  
    .mrl-sm-#{$i * 2} {
      margin-left: 2px * $i;
    }
    .mrl-sm-#{$i * 2}- {
      margin-left: -2px * $i;
    }
  
    .pdt-sm-#{$i * 2} {
      padding-top: 2px * $i;
    }
     
    .pdr-sm-#{$i * 2} {
      padding-right: 2px * $i;
    }
  
    .pdb-sm-#{$i * 2} {
      padding-bottom: 2px * $i;
    }
  
    .pdl-sm-#{$i * 2} {
      padding-left: 2px * $i;
    }
  
    .t-sm-#{$i * 2} {
      top: 2px * $i;
    }
  
    .t-sm-#{$i * 2}- {
      top: -2px * $i;
    }
  
    .r-sm-#{$i * 2} {
      right: 2px * $i;
    }
  
    .r-sm-#{$i * 2}- {
      right: -2px * $i;
    }
  
    .b-sm-#{$i * 2} {
      bottom: 2px * $i;
    }
  
    .b-sm-#{$i * 2}- {
      bottom: -2px * $i;
    }
  
    .l-sm-#{$i * 2} {
      left: 2px * $i;
    }
  
    .l-sm-#{$i * 2}- {
      left: -2px * $i;
    }
    .brtlr-sm-#{$i * 2} {
      border-top-left-radius: 2px * $i;
    }
  
    .brtrr-sm-#{$i * 2} {
      border-top-right-radius: 2px * $i;
    }
  
    .brbrr-sm-#{$i * 2} {
      border-bottom-right-radius: 2px * $i;
    }
  
    .brblr-sm-#{$i * 2} {
      border-bottom-left-radius: 2px * $i;
    }
  }
  
  @for $i from 0 to 81 {
    .fts-sm-#{$i} {
      font-size: 1px * $i;
    }
  }
}