@include xxl-min {

  .mxw-xxl-none {
    max-width: none;
  }

  .mxw-xxl-418 {
    max-width: 418px;
  }

  .mxw-xxl-114 {
    max-width: 114px;
  }

  .mxw-xxl-386 {
    max-width: 386px;
  }

  .mnw-xxl-505 {
    min-width: 505px;
  }
    
  .over-xxl-hid {
    overflow: hidden;
  }

  .over-xxl-vis {
    overflow: visible;
  }

  .over-xxl-au {
    overflow: auto;
  }

  .over-xxl-scro {
    overflow: scroll;
  }

  .over-xxl-ini {
    overflow: initial;
  }

  .over-xxl-inh {
    overflow: inherit;
  }

  .over-y-xxl-hid {
    overflow-y: hidden;
  }

  .br-xxl-0 {
    border: 0;
  }
  
  .brw-xxl-0 {
    border-width: 0;
  }
  
  .brw-xxl-1 {
    border-width: 1px;
  }
  
  .brw-xxl-1-3 {
    border-width: 1.3px;
  }
  
  .brw-xxl-2, .brw-xxl-2-f:focus {
    border-width: 2px;
  }
  
  .brw-xxl-2-5 {
    border-width: 2.5px;
  }
  
  .brw-xxl-3 {
    border-width: 3px;
  }
  
  .brw-xxl-4 {
    border-width: 4px;
  }
  
  .brw-xxl-5 {
    border-width: 5px;
  }
  
  .brlw-xxl-0 {
    border-left-width: 0;
  }
  
  .brlw-xxl-1 {
    border-left-width: 1px;
  }
  
  .brlw-xxl-2 {
    border-left-width: 2px;
  }
  
  .brlw-xxl-3 {
    border-left-width: 3px;
  }
  
  .brrw-xxl-0 {
    border-right-width: 0;
  }
  
  .brrw-xxl-1 {
    border-right-width: 1px;
  }
  
  .brrw-xxl-2 {
    border-right-width: 2px;
  }
  
  .brbw-xxl-0 {
    border-bottom-width: 0;
  }
  
  .brbw-xxl-1 {
    border-bottom-width: 1px;
  }
  
  .brbw-xxl-1-3 {
    border-bottom-width: 1.3px;
  }
  
  .brbw-xxl-2 {
    border-bottom-width: 2px;
  }
  
  .brbw-xxl-3 {
    border-bottom-width: 3px;
  }
  
  .brtw-xxl-0 {
    border-top-width: 0;
  }
  
  .brtw-xxl-1 {
    border-top-width: 1px;
  }
  
  .brs-xxl-sol {
    border-style: solid;
  }
  
  .brls-xxl-sol {
    border-left-style: solid;
  }
  
  .brts-xxl-sol {
    border-top-style: solid;
  }
  
  .brbs-xxl-sol {
    border-bottom-style: solid;
  }
  
  .brrs-xxl-sol {
    border-right-style: solid;
  }
  
  .brs-xxl-dash {
    border-style: dashed;
  }
  
  .brs-xxl-dot {
    border-style: dotted;
  }
  
  .brr-xxl-inh {
    border-radius: inherit;
  }
  
  .brr-xxl-50-p {
    border-radius: 50%;
  }
  
  .brtlr-xxl-inh {
    border-top-left-radius: inherit;
  }
  
  .brtrr-xxl-inh {
    border-top-right-radius: inherit;
  }
  
  .brbrr-xxl-inh {
    border-bottom-right-radius: inherit;
  }
  
  .brblr-xxl-inh {
    border-bottom-left-radius: inherit;
  }
  
  .brlc-xxl-tra {
    border-left-color: transparent;
  }
  
  .tra-xxl-br {
    border-color: transparent;
  }
  
  .pe-xxl-au {
    pointer-events: auto;
  }

  .pdb-xxl-0 {
    padding-bottom: 0;
  }

  .p-xxl-abs {
    position: absolute;
  }
  
  .p-xxl-fix {
    position: fixed;
  }
  
  .p-xxl-rel {
    position: relative;
  }
  
  .p-xxl-sta {
    position: static;
  }
  
  .p-xxl-sti {
    position: sticky;
  }
  
  .p-xxl-ini {
    position: initial;
  }

  .w-xxl-fl {
    width: 100%;
  }
  

  .l-xxl-50-p {
    left: 50%;
  }

  .l-xxl-au {
      left: auto;
  }

  .r-xxl-au {
    right: auto;
  }

  .t-xxl-au {
    top: auto;
  }

  .t-xxl-50-p {
    top: 50%;
  }

  .b-xxl-au {
    bottom: auto;
  }

  .t-trX-xxl-50-p {
      transform: translateX(-50%);
  }

  .t-trY-xxl-50-p- {
    transform: translateY(-50%);
  }

  .trsf-xxl-none {
    transform: none;
  }

  @for $i from 0 to 51 {
    .mrt-xxl-#{$i * 2} {
      margin-top: 2px * $i;
    }
    .mrt-xxl-#{$i * 2}- {
      margin-top: -2px * $i;
    }
    
    .mrr-xxl-#{$i * 2} {
      margin-right: 2px * $i;
    }
    .mrr-xxl-#{$i * 2}- {
      margin-right: -2px * $i;
    }
  
    .mrb-xxl-#{$i * 2} {
      margin-bottom: 2px * $i;
    }
    .mrb-xxl-#{$i * 2}- {
      margin-bottom: -2px * $i;
    }
  
    .mrl-xxl-#{$i * 2} {
      margin-left: 2px * $i;
    }
    .mrl-xxl-#{$i * 2}- {
      margin-left: -2px * $i;
    }
  
    .pdt-xxl-#{$i * 2} {
      padding-top: 2px * $i;
    }
     
    .pdr-xxl-#{$i * 2} {
      padding-right: 2px * $i;
    }
  
    .pdb-xxl-#{$i * 2} {
      padding-bottom: 2px * $i;
    }
  
    .pdl-xxl-#{$i * 2} {
      padding-left: 2px * $i;
    }
  
    .t-xxl-#{$i * 2} {
      top: 2px * $i;
    }
  
    .t-xxl-#{$i * 2}- {
      top: -2px * $i;
    }
  
    .r-xxl-#{$i * 2} {
      right: 2px * $i;
    }
  
    .r-xxl-#{$i * 2}- {
      right: -2px * $i;
    }
  
    .b-xxl-#{$i * 2} {
      bottom: 2px * $i;
    }
  
    .b-xxl-#{$i * 2}- {
      bottom: -2px * $i;
    }
  
    .l-xxl-#{$i * 2} {
      left: 2px * $i;
    }
  
    .l-xxl-#{$i * 2}- {
      left: -2px * $i;
    }
    .brtlr-xxl-#{$i * 2} {
      border-top-left-radius: 2px * $i;
    }
  
    .brtrr-xxl-#{$i * 2} {
      border-top-right-radius: 2px * $i;
    }
  
    .brbrr-xxl-#{$i * 2} {
      border-bottom-right-radius: 2px * $i;
    }
  
    .brblr-xxl-#{$i * 2} {
      border-bottom-left-radius: 2px * $i;
    }
  
    .mnh-xxl-#{$i * 2} {
      min-height: 2px * $i;
    }
  
    .mnw-xxl-#{$i * 2} {
      min-width: 2px * $i;
    }
  
    .mxh-xxl-#{$i * 2} {
      max-height: 2px * $i;
    }
  
    .mxw-xxl-#{$i * 2} {
      max-width: 2px * $i;
    }
  }
  
  @for $i from 0 to 81 {
    .fts-xxl-#{$i} {
      font-size: 1px * $i;
    }
  }
}