.swiper-button-disabled {
    opacity: .5 !important;
}

.dots {
    margin-left: -7px;
    margin-right: -7px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.dots > span {
    display: block;
    padding-left: 7px;
    padding-right: 7px;
    height: auto;
    width: auto;
    margin: auto;
    opacity: 1;
    background: none;
    border-radius: 0;
    margin: 0;
    position: relative;
    z-index: 1;

    &-active {
        opacity: 1;
    }
    
    &::before {
        content: '';
        display: block;
        height: 44px;
        width: 45px;
        background: transparent url(../img/carousel/dot.webp) no-repeat center;
    }
    
    &::after {
        content: '';
        display: block;
        position: absolute;
        top: -23px;
        bottom: -23px;
        left: -22px;
        right: -22px;
        z-index: 1;
        background: transparent url(../img/carousel/dot-active.webp) no-repeat center;
        opacity: 0;
        transition: all .3s ease;
    }

    &:hover::after {
        opacity: .4;
    }

    &.swiper-pagination-bullet-active::after {
        opacity: 1 !important;
    }
}