        opacity: 1;
        visibility: visible;
    }
}

.points-list {
    gap: 10px;
}

.points-link, .menu-link {
    height: 48px;
    width: 46px;
    position: relative;
    z-index: 1;
    color: var(--color-white);

    &::before {
        content: '';
        height: 100%;
        width: 100%;
        background: url("../img/points-nav-arrow.webp") no-repeat top left;
        background-size: 100% 100%;
        display: block;
        opacity: .50;
        z-index: -1;
        transition: .3s ease;
    }

    &::after {
        content: '';
        height: 5px;
        width: 23px;
        background: url("../img/points-nav-line.webp");
        display: block;
        position: absolute;
        left: 17px;
        top: 50%;
        transform: translateY(calc(50% - 4.5px));
        z-index: -2;
        opacity: 0;
        transition: .3s ease;
        margin-top: -1px;
    }
}

.points-link.active,
.points-item:hover .points-link,
.menu-link:hover,
.menu-link.active {
    transform: translate(5px, 0);
}

.points-link.active::before,
.points-item:hover .points-link::before,
.menu-link:hover::before,
.menu-link.active::before {
    transform: scale(1.3);
    opacity: 1;
}

.points-link.active::after,
.points-item:hover .points-link::after,
.menu-link:hover::after,
.menu-link.active::after {
    opacity: 1;
    left: 27px;
}

.points-link-name,
.menu-link-name {
    padding-left: 46px;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(7px, calc(-50% + 1px));
    line-height: 1.25;
    transition: transform .3s ease, opacity .3s ease, visibility .3s ease;
}

.points-link.active .points-link-name,
.points-item:hover .points-link-name,
.menu-link:hover .menu-link-name,
.menu-link.active .menu-link-name {
