        max-width: 291px;
    }

    .menu-link {
        padding-left: 10px;
    }

    .menu-link:hover::after, .menu-link.active::after {
        left: 37px;
    }
}

@include xl-min {
    .main-col--sidebar {
        max-width: 361px;
    }
    
    .main-col--391 {
        max-width: 391px;
    }

    .menu-link {
        padding-left: 26px;
    }

    .menu-link:hover::after, .menu-link.active::after {
        left: 53px;
    }
}