  .brw-xl-1 {
    border-width: 1px;
  }
  
  .brw-xl-1-3 {
    border-width: 1.3px;
  }
  
  .brw-xl-2, .brw-xl-2-f:focus {
    border-width: 2px;
  }
  
  .brw-xl-2-5 {
    border-width: 2.5px;
  }
  
  .brw-xl-3 {
    border-width: 3px;
  }
  
  .brw-xl-4 {
    border-width: 4px;
  }
  
  .brw-xl-5 {
    border-width: 5px;
  }
  
  .brlw-xl-0 {
    border-left-width: 0;
  }
  
  .brlw-xl-1 {
    border-left-width: 1px;
