    border-style: solid;
  }
  
  .brls-xl-sol {
    border-left-style: solid;
  }
  
  .brts-xl-sol {
    border-top-style: solid;
  }
  
  .brbs-xl-sol {
    border-bottom-style: solid;
  }
  
  .brrs-xl-sol {
    border-right-style: solid;
  }
  
  .brs-xl-dash {
    border-style: dashed;
  }
  
  .brs-xl-dot {
    border-style: dotted;
  }
  
  .brr-xl-inh {
    border-radius: inherit;
  }
  
  .brr-xl-50-p {
    border-radius: 50%;
  }
  
  .brtlr-xl-inh {
    border-top-left-radius: inherit;
  }
  
  .brtrr-xl-inh {
    border-top-right-radius: inherit;
  }
  
  .brbrr-xl-inh {
    border-bottom-right-radius: inherit;
  }
  
  .brblr-xl-inh {
    border-bottom-left-radius: inherit;
  }
  
  .brlc-xl-tra {
    border-left-color: transparent;
  }
  
  .tra-xl-br {
    border-color: transparent;
  }
  
  .pe-xl-au {
    pointer-events: auto;
  }

  .pdb-xl-0 {
    padding-bottom: 0;
  }

  .p-xl-abs {
    position: absolute;
  }
  
  .p-xl-fix {
