    right: auto;
  }

  .t-xl-au {
    top: auto;
  }

  .t-xl-50-p {
    top: 50%;
  }

  .b-xl-au {
    bottom: auto;
  }

  .t-trX-xl-50-p {
      transform: translateX(-50%);
  }

  .t-trY-xl-50-p- {
    transform: translateY(-50%);
  }

  .trsf-xl-none {
    transform: none;
  }

  @for $i from 0 to 51 {
    .mrt-xl-#{$i * 2} {
      margin-top: 2px * $i;
    }
    .mrt-xl-#{$i * 2}- {
      margin-top: -2px * $i;
    }
    
    .mrr-xl-#{$i * 2} {
      margin-right: 2px * $i;
    }
    .mrr-xl-#{$i * 2}- {
      margin-right: -2px * $i;
    }
  
    .mrb-xl-#{$i * 2} {
      margin-bottom: 2px * $i;
    }
    .mrb-xl-#{$i * 2}- {
      margin-bottom: -2px * $i;
    }
  
    .mrl-xl-#{$i * 2} {
      margin-left: 2px * $i;
    }
    .mrl-xl-#{$i * 2}- {
      margin-left: -2px * $i;
    }
  
    .pdt-xl-#{$i * 2} {
      padding-top: 2px * $i;
    }
     
    .pdr-xl-#{$i * 2} {
      padding-right: 2px * $i;
    }
  
    .pdb-xl-#{$i * 2} {
      padding-bottom: 2px * $i;
    }
  
    .pdl-xl-#{$i * 2} {
      padding-left: 2px * $i;
    }
  
    .t-xl-#{$i * 2} {
      top: 2px * $i;
    }
  
    .t-xl-#{$i * 2}- {
      top: -2px * $i;
    }
  
    .r-xl-#{$i * 2} {
