    }
  
    .b-xl-#{$i * 2}- {
      bottom: -2px * $i;
    }
  
    .l-xl-#{$i * 2} {
      left: 2px * $i;
    }
  
    .l-xl-#{$i * 2}- {
      left: -2px * $i;
    }
    .brtlr-xl-#{$i * 2} {
      border-top-left-radius: 2px * $i;
    }
  
    .brtrr-xl-#{$i * 2} {
      border-top-right-radius: 2px * $i;
    }
  
    .brbrr-xl-#{$i * 2} {
      border-bottom-right-radius: 2px * $i;
    }
  
    .brblr-xl-#{$i * 2} {
      border-bottom-left-radius: 2px * $i;
    }
  }
  
  @for $i from 0 to 81 {
    .fts-xl-#{$i} {
      font-size: 1px * $i;
    }
  }
