@include md-min {
    
  .mxw-md-100-p {
    max-width: 100%;
  }

  .over-md-hid {
    overflow: hidden;
  }

  .over-md-vis {
    overflow: visible;
  }

  .over-md-au {
    overflow: auto;
  }

  .over-md-scro {
    overflow: scroll;
  }

  .over-md-ini {
    overflow: initial;
  }

  .over-md-inh {
    overflow: inherit;
  }

  .over-y-md-hid {
    overflow-y: hidden;
  }

  .br-md-0 {
    border: 0;
  }
  
  .brw-md-0 {
    border-width: 0;
  }
  
  .brw-md-1 {
    border-width: 1px;
  }
  
  .brw-md-1-3 {
    border-width: 1.3px;
  }
  
  .brw-md-2, .brw-md-2-f:focus {
    border-width: 2px;
  }
  
  .brw-md-2-5 {
    border-width: 2.5px;
  }
  
  .brw-md-3 {
    border-width: 3px;
  }
  
  .brw-md-4 {
    border-width: 4px;
  }
  
  .brw-md-5 {
    border-width: 5px;
  }
  
  .brlw-md-0 {
    border-left-width: 0;
  }
  
  .brlw-md-1 {
    border-left-width: 1px;
  }
  
  .brlw-md-2 {
    border-left-width: 2px;
  }
  
  .brlw-md-3 {
    border-left-width: 3px;
  }
  
  .brrw-md-0 {
    border-right-width: 0;
  }
  
  .brrw-md-1 {
    border-right-width: 1px;
  }
  
  .brrw-md-2 {
    border-right-width: 2px;
  }
  
  .brbw-md-0 {
    border-bottom-width: 0;
  }
  
  .brbw-md-1 {
    border-bottom-width: 1px;
  }
  
  .brbw-md-1-3 {
    border-bottom-width: 1.3px;
  }
  
  .brbw-md-2 {
    border-bottom-width: 2px;
  }
  
  .brbw-md-3 {
    border-bottom-width: 3px;
  }
  
  .brtw-md-0 {
    border-top-width: 0;
  }
  
  .brtw-md-1 {
    border-top-width: 1px;
  }
  
  .brs-md-sol {
    border-style: solid;
  }
  
  .brls-md-sol {
    border-left-style: solid;
  }
  
  .brts-md-sol {
    border-top-style: solid;
  }
  
  .brbs-md-sol {
    border-bottom-style: solid;
  }
  
  .brrs-md-sol {
    border-right-style: solid;
  }
  
  .brs-md-dash {
    border-style: dashed;
  }
  
  .brs-md-dot {
    border-style: dotted;
  }
  
  .brr-md-inh {
    border-radius: inherit;
  }
  
  .brr-md-50-p {
    border-radius: 50%;
  }
  
  .brtlr-md-inh {
    border-top-left-radius: inherit;
  }
  
  .brtrr-md-inh {
    border-top-right-radius: inherit;
  }
  
  .brbrr-md-inh {
    border-bottom-right-radius: inherit;
  }
  
  .brblr-md-inh {
    border-bottom-left-radius: inherit;
  }
  
  .brlc-md-tra {
    border-left-color: transparent;
  }
  
  .tra-md-br {
    border-color: transparent;
  }
  
  .pe-md-au {
    pointer-events: auto;
  }

  .pdb-md-0 {
    padding-bottom: 0;
  }

  .p-md-abs {
    position: absolute;
  }
  
  .p-md-fix {
    position: fixed;
  }
  
  .p-md-rel {
    position: relative;
  }
  
  .p-md-sta {
    position: static;
  }
  
  .p-md-sti {
    position: sticky;
  }
  
  .p-md-ini {
    position: initial;
  }

  .w-md-fl {
    width: 100%;
  }
  

  .l-md-50-p {
    left: 50%;
  }

  .l-md-au {
      left: auto;
  }

  .r-md-au {
    right: auto;
  }

  .t-md-au {
    top: auto;
  }

  .t-md-50-p {
    top: 50%;
  }

  .b-md-au {
    bottom: auto;
  }

  .t-trX-md-50-p {
      transform: translateX(-50%);
  }

  .t-trY-md-50-p- {
    transform: translateY(-50%);
  }

  .trsf-md-none {
    transform: none;
  }

  @for $i from 0 to 51 {
    .mrt-md-#{$i * 2} {
      margin-top: 2px * $i;
    }
    .mrt-md-#{$i * 2}- {
      margin-top: -2px * $i;
    }
    
    .mrr-md-#{$i * 2} {
      margin-right: 2px * $i;
    }
    .mrr-md-#{$i * 2}- {
      margin-right: -2px * $i;
    }
  
    .mrb-md-#{$i * 2} {
      margin-bottom: 2px * $i;
    }
    .mrb-md-#{$i * 2}- {
      margin-bottom: -2px * $i;
    }
  
    .mrl-md-#{$i * 2} {
      margin-left: 2px * $i;
    }
    .mrl-md-#{$i * 2}- {
      margin-left: -2px * $i;
    }
  
    .pdt-md-#{$i * 2} {
      padding-top: 2px * $i;
    }
     
    .pdr-md-#{$i * 2} {
      padding-right: 2px * $i;
    }
  
    .pdb-md-#{$i * 2} {
      padding-bottom: 2px * $i;
    }
  
    .pdl-md-#{$i * 2} {
      padding-left: 2px * $i;
    }
  
    .t-md-#{$i * 2} {
      top: 2px * $i;
    }
  
    .t-md-#{$i * 2}- {
      top: -2px * $i;
    }
  
    .r-md-#{$i * 2} {
      right: 2px * $i;
    }
  
    .r-md-#{$i * 2}- {
      right: -2px * $i;
    }
  
    .b-md-#{$i * 2} {
      bottom: 2px * $i;
    }
  
    .b-md-#{$i * 2}- {
      bottom: -2px * $i;
    }
  
    .l-md-#{$i * 2} {
      left: 2px * $i;
    }
  
    .l-md-#{$i * 2}- {
      left: -2px * $i;
    }
    .brtlr-md-#{$i * 2} {
      border-top-left-radius: 2px * $i;
    }
  
    .brtrr-md-#{$i * 2} {
      border-top-right-radius: 2px * $i;
    }
  
    .brbrr-md-#{$i * 2} {
      border-bottom-right-radius: 2px * $i;
    }
  
    .brblr-md-#{$i * 2} {
      border-bottom-left-radius: 2px * $i;
    }
  
    .mnh-md-#{$i * 2} {
      min-height: 2px * $i;
    }
  
    .mnw-md-#{$i * 2} {
      min-width: 2px * $i;
    }
  
    .mxh-md-#{$i * 2} {
      max-height: 2px * $i;
    }
  
    .mxw-md-#{$i * 2} {
      max-width: 2px * $i;
    }
  }
  
  @for $i from 0 to 81 {
    .fts-md-#{$i} {
      font-size: 1px * $i;
    }
  }
}